export default {
  data() {
    return {
      InputBottom: 0
    };
  },
  computed: {
    highlight() {
      return this.$store.state.highlight;
    }
  },
  methods: {
    skip(path) {
      console.log(path, this.highlight, "跳转路径");
      this.$store.commit("setHighlight", path);
      uni.reLaunch({ url: path });
    }
  }
};
